import 'babel-polyfill';

import Vue from 'vue';
import VueRouter from 'vue-router';
import axios from 'axios';
import VueAxios from 'vue-axios';
import VueI18n from 'vue-i18n';
import Vuelidate from 'vuelidate';

import routerMap from './routers';
import locales from './i18n';
import store from './store';
import ServerApi from './serverApi';

import * as filters from 'common/js/filters';
import * as acs from 'common/js/acs';

require('login/scss/login.scss');

Vue.use(Vuelidate);
Vue.use(VueAxios, axios);
Vue.use(VueRouter);
Vue.use(VueI18n);
Vue.use(ServerApi);

axios.defaults.headers.common['x-csrf-token'] = acs.csrfToken;
axios.defaults.headers.common['acs-app-id'] = acs.getAppId();
axios.defaults.headers.common['acs-device-id'] = acs.getDeviceId();

var i18n = new VueI18n({
  locale: acs.locale,
  messages: locales
});

Object.keys(filters).forEach(function (k) {
  Vue.filter(k, filters[k]);
});

var transitionSlideLeftToRight = 'slide-right';
var transitionSlideRightToLeft = 'slide-left';

// insert popstate event listener before router,
// by doing so, we can change transition name while user press "Back" button
window.addEventListener('popstate', function (_) {
  store.commit('SET_TRANSITION_NAME', transitionSlideLeftToRight);
});

var router = routerMap(VueRouter);

router.afterEach(function (route) {
  Vue.nextTick(function (_) {
    return store.commit('SET_TRANSITION_NAME', transitionSlideRightToLeft);
  });
});

var App = new Vue({
  i18n: i18n,
  router: router,
  store: store
}).$mount('#app');


