import Vue from 'vue';
import Vuex from 'vuex';

import * as actions from './actions';
import * as getters from './getters';

import app from './modules/app';

Vue.use(Vuex);

var store = new Vuex.Store({
  strict: false,
  actions: actions,
  getters: getters,
  modules: {
    app: app
  },
  mutations: {}
});

export default store;


